.carousel {
    position: relative;
    height: 300px;
}

.carousel>button {
    height: 100%;
    width: 25px;
    position: absolute;
    opacity: 0;
    background-color: black;
    border: none;
    transition: opacity 0.3s ease;
}

.carousel>button:hover {
    opacity: 0.3;
}

.next {
    left: calc(100% - 25px);
}

.content {
    position: relative;
    left: 25px;
    z-index: -1;
    height: 100%;
    width: calc(100% - 50px);
    background-color: rgb(208, 229, 252);
}