.modal {
    border-radius: 20px;
    display: flex;
    background-color: cornflowerblue;
    position: absolute;
    width: 33%;
    height: 40%;
    top: -100%;
    left: calc(50% - (33% / 2));
    flex-direction: column;
    justify-content: center;
    z-index: 1000;
    animation: down 0.3s ease forwards;
}

.modal>p {
    margin: 20px;
    color: rgb(7, 92, 250);
    font-size: 20px;
}

.overlay {
    background-color: rgba(0, 0, 0, 0.526);
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
}

.toggle {
    display: flex;
    margin-left: 10%;
    margin-right: 10%;
    margin-bottom: 30px;
    justify-content: space-between;
}

.toggle>p {
    cursor: pointer;
    z-index: 1001;
    color: rgb(164, 193, 248);
}

.toggle>.active {
    color: rgb(7, 92, 250);
}

@keyframes down {
    from {
        top: -100%;
    }

    to {
        top: 20%;
    }
}