.input {
    margin-top: 10px;
    position: relative;
    left: 15%;
    border-radius: 20px;
    background-color: rgb(146, 176, 231);
    color: rgb(0, 89, 255);
    width: 70%;
    border: 1px solid rgb(72, 130, 238);
}

.button {
    margin-top: 20px;
    position: relative;
    left: 15%;
    border-radius: 20px;
    background-color: rgb(0, 89, 255);
    color: rgb(151, 186, 251);
    width: 70%;
    border: 2px solid rgb(0, 89, 255);
    cursor: pointer;
}