.header {
    margin: 20px;
    display: flex;
    height: 50px;
    align-items: center;

}

.logo {
    display: flex;
    align-items: center;
    position: relative;
    left: 0;
}

.logo>h1 {
    color: rgb(45, 45, 45);
}

.logo>button {
    margin-left: 10px;
    border: none;
    border-radius: 20px;
    height: 50px;
    cursor: pointer;
    background-color: rgb(144, 143, 143);
}

.sign_in {
    position: relative;
    left: 50%;
    border: none;
    border-radius: 20px;
    background-color: cornflowerblue;
    color: white;
    height: 50px;
    width: 50px;
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
}