.slide {
    display: flex;
    position: relative;
    height: 100%;
    justify-content: end;
    overflow: hidden;
}

.header {
    padding: 5px;
    width: fit-content;
    position: relative;
    top: -21px;
    right: -5px;
    background-color: cornflowerblue;
    opacity: 0.8;
    color: rgb(190, 213, 255);
    height: fit-content;
    border-bottom-left-radius: 20px;
}

.text_block {
    padding: 10px;
    background-color: cornflowerblue;
    position: absolute;
    top: 50px;
    margin-right: 40px;
    width: 50%;
    border-radius: 20px;
    color: rgb(190, 213, 255);
    font-size: 20px;
}

.image {
    transform: rotateZ(45deg);
    position: absolute;
    width: 300px;
    height: 250px;
    bottom: -20px;
    left: -80px;
    border-radius: 100%;
}