.slide {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.header {
    color: cornflowerblue;
}

.row {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60%;
}

.item {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: cornflowerblue;
    height: 100%;
    width: 30%;
    border-radius: 20px;
}

.item>img {
    height: 50%;
    width: 50%;
}

.item>p {
    text-align: center;
    word-wrap: break-word;
    color: rgb(190, 213, 255);
    ;
}